<template>
  <div>
    <yd-navbar title="NavBar">
      <router-link to slot="left">
        <yd-navbar-back-icon @click.native="toBack"></yd-navbar-back-icon>
      </router-link>
      <router-link to slot="right">
        <yd-navbar-next-icon></yd-navbar-next-icon>
      </router-link>
    </yd-navbar>
    {{data}}
  </div>
</template>
<script>
export default {
  data() {
    return {
      data: this.$route.query.data
    };
  },
  methods: {
    toBack() {
      console.log("进来了");
      this.$router.back(-1); //返回上一层
    }
  }
};
</script>

